/* div模拟的自适应的input输入框 */
.adaptive-input[data-v-f2963ccc] {
    overflow: hidden;
    word-break: keep-all;
    -o-text-overflow: clip;
       text-overflow: clip;
    white-space: nowrap;
    color: #606266;
    outline: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 35px;
    line-height: 2.0625rem;
    padding: 0 15px;
    border: 1px solid #E5E6E7;
    min-width: 120px;
    max-width: 100%;
    display: inline-block;
}
.adaptive-input.error[data-v-f2963ccc] {
    border-color: #f56c6c;
}
.adaptive-input.error[data-v-f2963ccc]:hover,
.adaptive-input.error[data-v-f2963ccc]:focus {
    border-color: #f56c6c;
}

/* contenteditable div模拟placeholder */
.adaptive-input[data-v-f2963ccc]:empty:before {
    content: attr(placeholder);
    color: #c0c4cc;
}

/*焦点时内容为空*/
.adaptive-input[data-v-f2963ccc]:focus:before {
    content: none;
}
.adaptive-input[data-v-f2963ccc]:hover,
.adaptive-input[data-v-f2963ccc]:focus {
    border-color: #2CBFBE;
}